import React from 'react';
import Link from 'gatsby-link';
import supersonicImg1 from "./static/images/supersonicImg1.jpg";
import supersonicImg2 from "./static/images/supersonicImg2.jpg";
import supersonicImg3 from "./static/images/supersonicImg3.jpg";
import supersonicImg4 from "./static/images/supersonicImg4.jpg";
import supersonicImg5 from "./static/images/supersonicImg5.jpg";
import supersonicImg6 from "./static/images/supersonicImg6.jpg";
import supersonicImg7 from "./static/images/supersonicImg7.jpg";
import supersonicImg8 from "./static/images/supersonicImg8.jpg";
import supersonicImg9 from "./static/images/supersonicImg9.jpg";
import supersonicImg10 from "./static/images/supersonicImg10.jpg";
import supersonicImg11 from "./static/images/supersonicImg11.jpg";
import supersonicImg12 from "./static/images/supersonicImg12.jpg";
import visacompanyImg from "./static/images/visacompanyImg.jpg";
import volkswagenEmailerImg from "./static/images/volkswagenEmailerImg.jpg";


const vh1supersonic = () => (
  <div className="fullBox">

    <div className="insideBanner supersonicBanner">
      <div className="bg supersonic"></div>
      <div className="midWrapper">
            <div className="rightSection"></div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h3>Vh1</h3>
                  <h1>SUPERSON<span>IC</span></h1>
                  <h2>SOCIAL MEDIA</h2>
                  <p>Client : Vh1 Supersonic<br />Work : Social Media Design</p>
                </div>
              </div>
              <div className="dot1"></div>
              <div className="dot2"></div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="supersonicContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="three">
            <li><img src={supersonicImg1} alt="" /></li>
            <li><img src={supersonicImg2} alt="" /></li>
            <li><img src={supersonicImg3} alt="" /></li>
            <li><img src={supersonicImg4} alt="" /></li>
            <li><img src={supersonicImg5} alt="" /></li>
            <li><img src={supersonicImg6} alt="" /></li>
            <li><img src={supersonicImg7} alt="" /></li>
            <li><img src={supersonicImg8} alt="" /></li>
            <li><img src={supersonicImg9} alt="" /></li>
            <li><img src={supersonicImg10} alt="" /></li>
            <li><img src={supersonicImg11} alt="" /></li>
            <li><img src={supersonicImg12} alt="" /></li>
          </ul>
        </div>
      </div>
  </div>

  <div className="midWrapper nextPrevious textAlignCenter">
    <ul>
      <li className="back">
        <Link to="/visacompany">
          <div className="thumb"> <img src={visacompanyImg} alt="" /> </div>
          <div className="button"><span>Back</span></div>
        </Link>
      </li>
      <li className="next">
        <Link to="/vwemailers">
          <div className="thumb"> <img src={volkswagenEmailerImg} alt="" /> </div>
          <div className="button"><span>next</span></div>
        </Link>
      </li>
    </ul>
  </div>


  </div>
)

export default vh1supersonic



